import React from 'react';
import './accueil.css';
import ToDoWidget from './ToDoWidget';

export default class AccueilMobile extends React.Component{
    render(){
        var div = {
            padding: '5px 15px',
        }
        return (
            <div style={div}>
                <h2>Un Widget test</h2>
                <ToDoWidget />
            </div>
        );
    }
}


