import React from "react";
import "./nav.css";
import TitreNav from "./titre_nav";
import CompElNav from "./comp_el_nav";
import {BrowserRouter as Router, Link} from 'react-browser-router';

class Nav extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            navigation: ["Accueil", "Recherche", "Projets", "Argent"],
        }
    }
    render(){
        return (
            <Router>
            <div id="navSideLeft">
                <TitreNav />
                {
                    this.state.navigation.map(el => <Link to={el}><CompElNav nowNav={this.props.nowNav} name={el} modifyNav={this.props.modifyNav}/></Link>)
                }
            </div>
            </Router>
        );
    }
}

export default Nav;


