import React from 'react';
export default class ToDoWidget extends React.Component{


    render(){

        var h4 = {
            fontSize: '35px',
            color: '#fff9',
            margin: '0',
            padding: '0',
        }
        var blackInside = {
            backgroundColor: '#0000004d',
            color: 'white',
            borderRadius: '5px',
            padding: '0',
            marginTop: '15px',  
        }


        return(
            <div class="todoWidget shadow">
                <h4 style={h4}>To-Do</h4>
                <div style={blackInside}>
                    <div class="todoWidgetInside">
                        test yolo
                    </div>
                    <div class="todoWidgetInside">
                        test yolo
                    </div>
                    <div class="todoWidgetInside">
                        test yolo
                    </div>
                    <div class="todoWidgetInside">
                        test yolo
                    </div>
                </div>
            </div>
        );
    }
}


